@import url("https://fonts.googleapis.com/css2?family=Metropolis:wght@400;500;600;700&display=swap");

body {
  margin: 0px;
}

* {
  font-family: "Metropolis", sans-serif !important;
}

*::-webkit-scrollbar {
  width: 3px !important;
  height: 3px !important;
}

*::-webkit-scrollbar-track {
  background-color: #eee;
}

*::-webkit-scrollbar-thumb {
  background: #ebebeb;
}

*::-webkit-scrollbar-thumb:hover {
  background: rgb(224, 223, 223);
}

@import "./helpers/styles.scss";

@include Spacings;
@include Sizing;
@include Opacity;
@include HWs;

.w-full {
  width: 100%;
}

p
 {
  font-size: 1rem;
}

h1 {
  // font-size: 2rem;
  font-size: 1rem;
}

h2 {
  font-size: 1.7rem;
}

h3 {
  font-size: 1rem;
  // font-size: 1.5rem;
}

h4 {
  font-size: 1.3rem;
}

h3 {
  font-size: 1.1rem;
}

.ant-layout {
  max-height: 100dvh;
  overflow-y: auto;
}

.title {
  font-size: 26px;
  white-space: nowrap;

  @include Responsive("tab") {
    font-size: 22px;
    letter-spacing: -0.5px;
  }
}

.menu {
  overflow-y: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #f9f9f9;
  border-right: 1px solid #eee;
  position: fixed;
  z-index: 999;
}

.menu-item {
  padding-top: 25px;
  padding-bottom: 25px;

  &.collapsed {
    padding-top: 0px;
    padding-bottom: 0px;
  }
}

.ant-select-selector {
  padding: 0 5px !important;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

.justify-around {
  justify-content: space-around;
}

.justify-end {
  justify-content: flex-end;
}

.c-green {
  color: $green;
}

.c-white {
  color: white !important;
}

.c-red {
  color: $red;
}

.c-primary {
  color: $primary;
}

.c-gray {
  color: $gray ;
}

.c-darkgray {
  color: $darkgray;
}

.bg-opaque {
  background-color: $blue_opaque !important;
}

.bg-primary {
  background-color: $primary !important;
}

.c-pointer {
  cursor: pointer;
}

.display-none {
  display: none;
}

.fw-500 {
  font-weight: 500 !important;
  font-size: 14px !important;
}
.fw-14 {
  font-weight: 500 !important;
  font-size: 14px !important;
}
.fw-500-14 {
  font-weight: 500 !important;
  font-size: 14px !important;
}

.fw-600-16 {
  font-weight: 600 !important;
  font-size: 14px !important;
}
.fw-600 {
  font-weight: 600 !important;
}
.fw-700 {
  font-weight: 700 !important;
  font-size: 14px !important;
}

.border-b {
  border-bottom: 1px solid #eee;
}

.uppercase {
  text-transform: uppercase !important;
}

.text-center {
  text-align: center;
}

.object-contain {
  object-fit: contain;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.capitalize {
  text-transform: capitalize !important;
}

.MuiStepIcon-root.MuiStepIcon-active,
.MuiStepIcon-root.MuiStepIcon-completed {
  color: $primary !important;
}

.mx-auto {
  margin-left: auto !important;
  margin-right: auto !important;
}
