@import "../../helpers/styles.scss";

.auth-layout {
  background: url("https://wallpapercave.com/wp/wp11926801.jpg");
  background-color: #d6f2fe7c;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: lighten;
  box-shadow: 1px 15px 40px #000000;
  background-size: cover;
  height: 100dvh;
  width: 100dvw;
  overflow: hidden;
  display: grid;
  place-items: center;
  .auth-card {
    height: 80vh;
    width: 70vw;
    background: white;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    overflow: hidden;

    img {
      width: 90%;
      height: 100%;
    }
  }
  .fieldWidth {
    width: 99%;
  }
  .signupScroll {
    overflow: scroll;
    scrollbar-width: none;
    // overflow-x: hidden;
  }
  @media only screen and (max-width: 768px) {
    .auth-card {
      height: 90vh;
      display: flex;
      flex-direction: column;
      overflow: scroll;
      scroll-behavior: smooth;
      scrollbar-width: none;
      //  overflow-x: hidden;
    }
    .fieldWidth {
      width: 110%;
    }
    .signupScroll {
      overflow: auto;
      // overflow-x: hidden;
    }
  }
}


// new css rules for new login page design
.container {
  height: 100vh;
  width: 100vw;
  display: flex;
}

.leftHalf,
.righthalf {
  width: 50vw;
}

.mainPageImage {
  height: 23.07vw;
  width: 28.9vw;
  margin-left: 10.55vw;
  margin-top: 13.4vw;
}

.smartUdyogLogoImage {
  width: 8.68vw;
  height: 4.69vw;
  margin-left: 12.62vw;
  margin-top: 5.73vw;
  margin-bottom: 2.5vw;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.inputLabelText {
  font-family: "Metropolis", sans-serif;
  font-size: 1.39vw;
  font-weight: 500;
  line-height: 2.08vw;
  text-align: left;
  color: #8c8c8c;
  margin: 0;
}

.input-field {
  width: 33vw;
  height: 3.47vw;
  border: 1px solid #3e3e3e;
  border-radius: 0.46vw;
  font-size: 1.39vw;
  font-weight: 700;
  line-height: 2.08vw;
  text-align: left;
  margin-top: 0.75vw;
  margin-bottom: 3vw;
  padding-left: 1em;
  padding-right: 1em;
  box-sizing: border-box;
}

.login-btn {
  width: 33vw;
  height: 3.47vw;
  border-radius: 0.46vw;
  background-color: #8c8c8c;
  color: white;
  font-size: 1.39vw;
  font-weight: 500;
  line-height: 2.08vw;
  text-align: center;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-btn.enabled {
  background-color: #3E3E3E; /* Green when enabled */
  color: white;
}

.login-btn.disabled {
  background-color: #8C8C8C; /* Gray when disabled */
  cursor: not-allowed;
}

.login-help-text {
  font-size: 1.16vw;
  line-height: 1.39vw;
  color: #8c8c8c;
  margin-top: 5vw;
}

.link-container {
  margin-top: 1.5vw;
}

.tnc-link, .pp-link {
  text-decoration: none;
  color: #1535db;
  font-size: 0.81vw;
  font-weight: 500;
  line-height: 0.81vw;
  text-align: left;
}
